import React, {Fragment, useState} from 'react';
import './NavigationMenu.css'
import {NavLink} from "react-router-dom";
import {routes} from "../../../Routes/Routes";
import {useOnClickOutside} from "../../../Hooks/UseOnClickOutside";
import {words} from "../../../Words/Words";

function NavigationMenu(props) {

    return (
        <nav className={'navigationMenu'}>
            <ul className={'menu'}>
                <li>
                    <NavLink exact activeClassName={'active'} to={routes.home.path}>
                        {words.home}
                    </NavLink>
                </li>
                <li>
                    <NounsLink/>
                </li>
                <li>
                    <VerbsLink/>
                </li>
            </ul>
        </nav>
    )
}

function NounsLink(props) {

    const [ref] = useOnClickOutside(() => toggleExpanded(false));
    const [expanded, toggleExpanded] = useState(false);
    const handleClick = () => toggleExpanded(!expanded);

    return (
        <div ref={ref}>
            <div className={'link'} onClick={handleClick}>
                {words.nouns}
            </div>

            {
                expanded && (
                    <ul className={'subMenu'}>
                        <li>
                            <NavLink to={routes.nouns.routes.learn.path} onClick={handleClick}>{words.learn}</NavLink>
                        </li>
                        <li>
                            <NavLink to={routes.nouns.routes.repeat.path} onClick={handleClick}>{words.repeat}</NavLink>
                        </li>
                        <li>
                            <NavLink to={routes.nouns.routes.all.path} onClick={handleClick}>{words.all}</NavLink>
                        </li>
                    </ul>
                )
            }

        </div>
    )
}

function VerbsLink(props) {

    const [ref] = useOnClickOutside(() => toggleExpanded(false));
    const [expanded, toggleExpanded] = useState(false);
    const handleClick = () => toggleExpanded(!expanded);

    return (
        <div ref={ref}>
            <div className={'link'} onClick={handleClick}>
                {words.verbs}
            </div>

            {
                expanded && (
                    <ul className={'subMenu'}>
                        <li>
                            <NavLink to={routes.verbs.routes.learn.path} onClick={handleClick}>{words.learn}</NavLink>
                        </li>
                        <li>
                            <NavLink to={routes.verbs.routes.repeat.path} onClick={handleClick}>{words.repeat}</NavLink>
                        </li>
                        <li>
                            <NavLink to={routes.verbs.routes.all.path} onClick={handleClick}>{words.all}</NavLink>
                        </li>
                    </ul>
                )
            }

        </div>
    )
}


export default NavigationMenu;