import React, {useState} from 'react';
import './ListOfVerbs.css'
import verbs from '../../Data/Verbs.json'

function ListOfVerbs({language}) {

    // function shuffle(a) {
    //     for (let i = a.length - 1; i > 0; i--) {
    //         const j = Math.floor(Math.random() * (i + 1));
    //         [a[i], a[j]] = [a[j], a[i]];
    //     }
    //     return a;
    // }
    //
    // console.log(JSON.stringify(shuffle(verbs)));

    const data = verbs.filter((verb, index) => {
        if (index >= 0 && index < 10) {
            return verb
        }
        return null;
    });

    return (
        <div className={'listOfVerbs'}>
            <ul>

                {
                    data.map(element => <Verb {...element} key={element.icelandic} language={language}/>)
                }

            </ul>
        </div>
    );
}

function Verb(props) {

    const [translation, toggleTranslation] = useState(false);

    const handleClick = () => {
        toggleTranslation(true);
        setTimeout(()=>toggleTranslation(false),1000);
    };


    return (
        <li onClick={handleClick}>
            <span>

                {
                    !translation ? props.icelandic
                        : props.language ? props.english[0]
                        : props.russian[0]
                }

            </span>
        </li>
    )
}

export default ListOfVerbs;