import React from "react";
import './LanguageSelection.css'
import {words} from "../../Words/Words";

function LanguageSelection({language, switchLanguage}) {

    const handleChange = () => switchLanguage(!language);

    return (
        <div className={'languageSelection'}>
            <label>
                <input type={'checkbox'} checked={language} onChange={handleChange}/>
                <div className={'checkbox'}>
                    {words.english}
                </div>
            </label>
            <label>
                <input type={'checkbox'} checked={!language} onChange={handleChange}/>
                <div className={'checkbox'}>
                    {words.russian}
                </div>
            </label>
        </div>
    )
}

export default LanguageSelection;